const fs = require('fs');
const glob = require('glob');
const xml2js = require('xml2js');
const path = require('path');

export function getTranslationSpecs(options = {}) {
  try {
    const specProps = getSpecProps(options);
    const files = glob.sync(path.join(options.base || process.cwd(), options.path || '/@(app|src)/**/*_en.json'));
    return files.reduce((specs, fileName) => {
      const singleFileSpecs = createSpecsFromFile(fileName, specProps);
      return Object.assign(specs, singleFileSpecs);
    }, {});
  } catch (e) {
    console.warn('ab-translate - could not find translation keys', e);
    return {};
  }
}

const defaultSpecProps = {
  onlyForLoggedInUsers: true
};

function getSpecProps(options) {
  const specProps = {
    ...defaultSpecProps,
    ...getProjectProps(options)
  };

  if ('onlyForLoggedInUsers' in options) {
    specProps.onlyForLoggedInUsers = options.onlyForLoggedInUsers;
  }

  if ('scopes' in options) {
    specProps.scopes = options.scopes;
  }

  return specProps;
}

function getProjectPomFile(pomFileName) {
  try {
    return fs.readFileSync(pomFileName, 'utf-8');
  } catch (e) {
    return null;
  }
}

function getProjectProps(options) {
  const pomFileContent = getProjectPomFile(options.pom || 'pom.xml');
  if (!pomFileContent) {
    return {};
  }
  const parser = new xml2js.Parser();
  let res;
  parser.parseString(pomFileContent, (err, result) => {
    if (err) {
      console.warn(err);
      console.warn('please make sure your pom.xml is valid');
    } else if (result.project && result.project.developers && result.project.developers[0] && result.project.developers[0].developer && result.project.developers[0].developer[0]) {
      const email = parseTextNode(result.project.developers[0].developer[0].email);
      const artifactId = parseTextNode(result.project.artifactId);
      res = {owner: email, scopes: [artifactId]};
    } else {
      console.warn('please make sure your pom.xml is valid');
    }
  });
  return res;
}

function parseTextNode(arr) {
  return arr.join('').trim();
}

function createSpecsFromFile(fileName, {owner, scopes, onlyForLoggedInUsers}) {
  try {
    const fileContent = JSON.parse(fs.readFileSync(fileName, 'utf-8'));
    return Object.keys(fileContent)
      .filter(translationKey => /.*\|abTranslate_\d+\|?.*/.test(translationKey))
      .reduce((acc, translationKey) => {
        const [, baseTranslationKey, group, specName] = /(.*)\|abTranslate_(\d+)\|?(.*)/.exec(translationKey);
        const specKey = `specs.abTranslate.${scopes[0]}.${specName ? 'abSpec-' + specName : baseTranslationKey}`;
        if (!acc[specKey]) {
          acc[specKey] = {
            owner,
            scopes,
            onlyForLoggedInUsers,
            controlGroup: '0',
            variants: [group]
          };
        } else if (!acc[specKey].variants.includes(group)) {
          acc[specKey].variants.push(group);
        }
        return acc;
      }, {});
  } catch (e) {
    console.warn('ab-translate - could not read file', fileName);
    return {};
  }
}
